<template>
  <div>
    <Titletop title="个人中心">
      <router-link slot="l" replace to="/mineself"></router-link>
      <router-link slot="r" replace to="/index"></router-link>
    </Titletop>
    <PersonalSettings>
      <div class="basic">
        <div class="basictop">
          <span>昵称</span>
          <input type="text" maxlength="10" v-model="name" />
          <span>{{ name.length }}/10</span>
          <h4>原用户名称{{ userapp.author_name }}</h4>
        </div>
        <div class="gender">
          <van-radio-group v-model="radio" direction="horizontal">
            <van-radio name="1"><img src="@/assets/img/nan.png" /></van-radio>
            <van-radio name="2"><img src="@/assets/img/nv.png" /></van-radio>
            <van-radio name="0">保密</van-radio>
          </van-radio-group>
        </div>
        <button @click="submit">提交</button>
      </div>
    </PersonalSettings>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import PersonalSettings from "@/components/PersonalSettings.vue";

export default {
  name: "Mymysics",
  components: { Titletop, PersonalSettings },
  props: ["gets"],
  data() {
    return {
      radio: "",
      name: "",
      userapp: JSON.parse(localStorage.getItem("userapp")),
    };
  },
  methods: {
    submit() {
      if (!this.radio) {
        this.$toast("请选择性别");
        return;
      }

      this.axios
        .post(
          "https://jyapi.jyacg.com/admin/user/personupbase",
          { table: 1, nickname: this.name, sex: this.radio },
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          if (res.data.code == 1) {
            this.name = ''
            this.$dialog({
              title: "温馨提示",
              message: "你已经修改过不能修改了",
            });
            return
          }
          this.$router.replace({
            name: "mineself",
          });
          this.gets();
        });
    },
  },
  mounted() {
    this.gets();
  },
};
</script>

<style lang="scss" scoped>
.basic {
  width: 250px;
  height: 200px;
  margin: 21px 37.5px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .basictop {
    height: 55px;
    width: 250px;
    text-align: center;

    border: none;
    span {
      display: inline-block;
      font-size: 15px;
      color: #333;
      margin: 0px 9px;
      width: 30px;
    }
    input {
      height: 25px;
      width: 130px;
      border-radius: 15px;
      border: 1px solid #333;
    }
    h4 {
      margin-top: 10px;
      font-size: 13px;
      color: #999;
      font-weight: 700;
    }
  }
  .gender {
    margin: 15px 0px 30px;
    width: 250px;

    border: none;
    .van-radio-group {
      display: flex;
      border: none;
      justify-content: space-between;
      .van-radio {
        height: 21px;
      }
      display: flex;
    }
    img {
      margin-top: 5px;
      width: 20px;
      height: 20px;
    }
  }
  button {
    width: 118px;
    height: 30px;
    background-color: #439fff;
    color: #fff;
    border: none;
    border-radius: 15px;
  }
}
</style>